import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'

import Header from '../components/header'
import './index.css'
import './fonts.css'

import Gapi from '../components/Google/Gapi.jsx';
import GA from '../components/Google/GA.jsx';

class Layout extends React.Component{
  render(){
    let { children, data } = this.props;
    return(
        <div>
            <Helmet
              title={data.site.siteMetadata.title}
              meta={[
                { name: 'description', content: 'Sample' },
                { name: 'keywords', content: 'sample, something' },
              ]}
              link={[
                //  {rel: 'stylesheet', type:'text/css', href:'https://cdnjs.cloudflare.com/ajax/libs/semantic-ui/2.2.2/semantic.min.css'},
                {rel: 'stylesheet', type:'text/css', href:'//cdn.jsdelivr.net/chartist.js/latest/chartist.min.css'}
              ]}
            />
            <Header siteTitle={data.site.siteMetadata.title} />
            <GA />
            {/* <Gapi /> */}
            <div
              style={{
                margin: '0 auto',
                maxWidth: 960,
                padding: '0px 1.0875rem 1.45rem',
                paddingTop: 0,
              }}
            >
              {children()}
            </div>
          </div>
    )
  }
}

Layout.propTypes = {
  children: PropTypes.func,
}

export default Layout

export const query = graphql`
  query SiteTitleQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`



