import React from 'react'
import {connect} from 'react-redux';
import {setSignedIn} from './actions.js';
import {bindActionCreators} from 'redux';

const API_KEY = 'AIzaSyBc2-uDprThmL1avRG6W0BHLqVVhOoxcF0';
const CLIENT_ID = '640363567361-na8ad159n3hsoa0tugsves1o8n6crsti.apps.googleusercontent.com';
// Array of API discovery doc URLs for APIs used by the quickstart
const DISCOVERY_DOCS = ["https://www.googleapis.com/discovery/v1/apis/drive/v3/rest"];
// Authorization scopes required by the API; multiple scopes can be
// included, separated by spaces.
var SCOPES = 'https://www.googleapis.com/auth/drive.metadata.readonly';

//Client ID	640363567361-na8ad159n3hsoa0tugsves1
//      640363567361-na8ad159n3hsoa0tugsves1o8n6crsti.apps.googleusercontent.com
//API key AIzaSyBc2-uDprThmL1avRG6W0BHLqVVhOoxcF0

class Gapi extends React.Component{
    constructor(props){
        super(props);
        console.log("running google api");
        this.loadApi = this.loadApi.bind(this);
        this.initClient = this.initClient.bind(this);

        this.updateSigninStatus = this.updateSigninStatus.bind(this);
        this.setupGapi = this.setupGapi.bind(this);
        this.renderFiles = this.renderFiles.bind(this);
        this.state={
            loaded: false,
            authorizeButton: true,
            list: 'no files'
        }
    }
    componentDidMount() {
        this.loadApi();
    }

    loadApi(){
        const script = document.createElement("script");
        //script.src = "https://apis.google.com/js/platform.js";
        //script.src = "https://apis.google.com/js/plusone.js";
        script.src = 'https://apis.google.com/js/api.js';
        script.onload = () => {
            console.log("gapi loaded the script");
            gapi.load('client', () => {
                console.log("gapi embedding post....");
                console.log(gapi);
                //gapi.post.render("gapipost", {'href' : 'https://plus.google.com/100326176790146306456/posts/7EWoGL1eLij'} );
                gapi.load('client:auth2', this.initClient);
            });
        // gapi.load('client', () => {
        //     gapi.client.setApiKey(API_KEY);
        //     gapi.client.load('youtube', 'v3', () => {
        //     this.setState({ gapiReady: true });
        //     });
        // });
        };

        document.body.appendChild(script);
    }

    updateSigninStatus(isSignedIn) {
              /**
       *  Called when the signed in status changes, to update the UI
       *  appropriately. After a sign-in, the API is called.
       */
        console.log("update sign in status " + isSignedIn);
        this.props.setSignedIn(isSignedIn);
        if (isSignedIn) {
        //   authorizeButton.style.display = 'none';
        //   signoutButton.style.display = 'block';
        //  this.setState({authorizeButton:false})
          //this.listFiles();
        } else {
        //   authorizeButton.style.display = 'block';
        //   signoutButton.style.display = 'none';
         // this.setState({authorizeButton:true})
        }
      }
      initClient(){
        console.log("initiating client");
        let mythis = this;
        gapi.client.init({
            apiKey: API_KEY,
            clientId: CLIENT_ID,
            discoveryDocs: DISCOVERY_DOCS,
            scope: SCOPES
        }).then(this.setupGapi);
    }
    setupGapi(){
        console.log("gapi this");
        console.log(this);
        gapi.auth2.getAuthInstance().isSignedIn.listen(this.updateSigninStatus);

        // Handle the initial sign-in state.
        this.updateSigninStatus(gapi.auth2.getAuthInstance().isSignedIn.get());
    }

    listFiles() {
        console.log("listing files");
        gapi.client.drive.files.list({
          'pageSize': 10,
          'fields': "nextPageToken, files(id, name)"
        }).then(this.renderFiles);
      }
      renderFiles(response){
        var files = response.result.files;
        if (files && files.length > 0) {
            let list = files
            .map(f => <span>{f.name} has id {f.id}</span>)
            .reduce((prev, curr) => [prev, ', ', curr])
          // for (var i = 0; i < files.length; i++) {
          //   var file = files[i];
          //   appendPre(file.name + ' (' + file.id + ')');
          // }
          this.setState({
              list
          })
        }
      }
    render(){
        return <div>
            <div id="commentscounter"></div>
            <div id="gapipost">
            </div>
            <button onClick={()=>{this.listFiles()}}>List Files</button>
            {this.state.list}
            {this.props.gapi.isSignedIn? <p>is not signed in</p>:<p>is signed in</p>}
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        gapi: state.gapi
    };
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
            setSignedIn
        }, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Gapi);

