const options = [ { key: 'Arabic', text: 'Arabic', value: 'Arabic' }]
class Header extends React.Component {
  constructor(props){
    super(props);
    console.log("header");
    console.log(props);
    this.state = { activeItem: 'home' }
  }
  handleItemClick = (e, { name }) => this.setState({ activeItem: name })

  render() {
    const { activeItem } = this.state

    return (
      <div>
        <Title><Link exact to="/">{this.props.siteTitle}</Link></Title>
        {/* <SignInBtn /> */}
        <Menu>
          <MenuItem><Link exact to="/" activeClassName="active">Posts</Link></MenuItem>
          <MenuItem><Link exact to="/games" activeClassName="active">Games</Link></MenuItem>
          <MenuItem><Link exact to="/options" activeClassName="active">Options</Link></MenuItem>
        </Menu>
        </div>
    //   <Menu>
    //     <MyMenuBtn>
    //       <Menu.Item>
    //         <Link exact to="/" activeClassName="active">{this.props.siteTitle}</Link>
    //       </Menu.Item>
    //     </MyMenuBtn>
    //     <MyMenuBtn>
    //       <Menu.Item>
    //         <Link exact to="/games" activeClassName="active">Games</Link>
    //       </Menu.Item>
    //     </MyMenuBtn>
    //     <Menu.Menu position='right'>
    //       <Dropdown text='Shopping' pointing className='link item'>
    //           <Dropdown.Menu>
    //                 <Dropdown.Header>Mens</Dropdown.Header>
    //                 <Dropdown.Item>Shirts</Dropdown.Item>
    //                 <Dropdown.Item>Pants</Dropdown.Item>
    //                 <Dropdown.Divider />
    //                 <Dropdown.Header>Womens</Dropdown.Header>
    //                 <Dropdown.Item>Dresses</Dropdown.Item>
    //           </Dropdown.Menu>
    //       </Dropdown>
    //       </Menu.Menu>
    // </Menu>
    )
  }
}


export default Header


const Menu = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 5vh;
`
const MenuItem = styled.span`
  a {
    text-decoration:none;
    transition: all 0.5s ease-in-out;
    padding: 10px 30px 10px 30px;
    letter-spacing: 5px;
    border-bottom: 2px transparent solid;
  }
  a .active{
    border-bottom: 2px black solid;
  }
  a:hover{
    border-bottom: 2px black solid;
  }
`
const Title = styled.h1`
  text-align:center;
  font-size: 1.5em;
  margin-top:20px;
  margin-bottom: 10px;
  a{
    text-decoration: none;
  }
`


